@font-face {
  font-family: Avenir;
  src: url("../fonts/Avenir/AvenirLTStd-Light.otf");
}

@font-face {
  font-family: AvenirMedium;
  src: url("../fonts/Avenir/AvenirLTStd-Medium.otf");
}

@font-face {
  font-family: AvenirHeavy;
  src: url("../fonts/Avenir/AvenirLTStd-Heavy.otf");
}

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: transparent;
  box-sizing: border-box;
  max-height: 100vh;
  overflow-y: hidden !important;
}

.main {
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 100vh;
  max-height: 100vh;
  overflow-y: hidden !important;
}

.modal-container {
  height: 700px;
}
.modal-content {
  flex-direction: row;
}
.image-container {
  width: 50%;
}
.image-expand-button {
  bottom: 0;
}
.image-left-button {
  left:0;
  bottom:50%;
}
.image-right-button {
  right:50%;
  bottom:50%;
}
.checkout-view {
  width: 50%;
}
@media screen and (max-width: 1023px) {
  .desktop-only {
    display: none !important;
  }
  .details-view {
    max-height: 80vh !important;
    overflow-y: auto;
    padding: 64px 32px !important;
  }
  .modal-container {
    height: 80vh !important;
    width: 80vw !important;
    overflow-x: hidden;
    overflow-y: hidden;
  }
  .modal-content {
    flex-direction: column;
    max-height: 80vh;
    overflow-y: scroll;
  }
  .image-container {
    width: 100%;
    min-height: 80vh !important;
    background-color: white !important;
  }
  .product-image {
    width: 538px;
    height: 538px;
    min-height: 80vh !important;
    margin: 0 auto;
  }
  .image-expand-button {
    display: flex !important;
    position: relative !important;
    top: unset !important;
    left: unset !important;
    right: unset !important;
    bottom: unset !important;
    margin-top: -39px;
  }
  .showClickableOnHover:hover>.image-expand-button {
    margin-top: 218px !important;
  }
  .image-left-button {
    position: relative !important;
    top: unset;
    left: unset;
    right: unset;
    bottom: unset;
    margin-top: -289px;
    margin-right: auto;
  }
  .image-right-button {
    position: relative !important;
    top: unset;
    left: unset;
    right: unset;
    bottom: unset;
    margin-top: -48px;
    margin-left: auto;
  }
  .add-to-bag-button {
    z-index: 100000;
    position: absolute !important;
    bottom: 0;
    left: 0;
    min-width: calc(100% - 5px);
    padding: 40px 64px 40px 64px !important;
    background-color: white !important;
    box-shadow: 0px 0px 8px 10px rgba(0, 0, 0, 0.05);
  }
  .product-value {
    z-index: 0 !important;
  }
  .product-details {
    min-width: 100% !important;
    margin-bottom: 164px;
  }
  .checkout-view {
    width: 80%;
  }
}
@media screen and (max-width: 600px) {
  .modal-container {
    height: 100vh !important;
    width: 100vw !important;
    padding: unset !important;
    margin: unset !important;
  }
  .modal-content {
    flex-direction: column;
    max-height: 100%;
    overflow-y: scroll;
  }
}

.clickable {
  display: flex;
  justify-content: center;
  user-select: none;
  cursor: pointer;
}

.hidden-clickable {
  display: none;
  justify-content: center;
  user-select: none;
  cursor: pointer;
}

.menu-svg {
  width: 13px;
  height: 13px;
  margin: auto 0;
}

.size-select-boxes {
  display: flex;
  flex-direction: row;
  align-items: left;
  justify-content: left;
}

.size-select-boxes>div{
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;

  width: 48px;
  height: 48px;
  border: 1px solid #949494;
  margin: 0 12px 0 12px;
  padding: 0 0 0 0;

  user-select: none;
  cursor: pointer;
}

.showClickableOnHover:hover>.hidden-clickable {
  display: flex !important;
}

.out-of-stock {
  position: absolute;
  width: 68px;
  margin-top: -48px;
  margin-left: 20px;
  border-bottom: 1px solid #949494;
  transform: rotate(45deg);
  transform-origin: top left;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

::-webkit-scrollbar-thumb {
  background-color: rgb(202, 202, 202);
}
